.button {
  height: 44px;
  width: 44px;
  background-image: url(../../images/btn.svg);
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  border-radius: 8px;
  margin-left: 10px;
  cursor: pointer;
  transition: all .2s ease;
}

.button:hover {
  opacity: .7;
}